import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { NzCodeBoxModule } from "app/showcase/share/nz-codebox/nz-codebox.module";
import { EchartsDemoRoutingModule } from "app/showcase/custom-components/echarts-demo/echarts-demo.routing.module";
import { NzGridModule } from "app/showcase/share/nz-grid/nz-grid.module";
import { NzToolTipModule } from "app/showcase/share/nz-tooltip/nz-tooltip.module";
import { EchartsDemoMapComponent } from "app/showcase/custom-components/echarts-demo/echarts-demo-map.component";
import { EchartsDemoBarComponent } from "app/showcase/custom-components/echarts-demo/echarts-demo-bar.component";
import { EchartsDemoComponent } from "app/showcase/custom-components/echarts-demo/echarts-demo.component";
import { EchartsModule } from "app/custom-components/echarts/echarts";
import { EchartsDemoLineComponent } from "app/showcase/custom-components/echarts-demo/echarts-demo-line.component";
import { EchartsDemoOptionsComponent } from "app/showcase/custom-components/echarts-demo/echarts-demo-options.component";
import { EchartsDemoPieComponent } from "app/showcase/custom-components/echarts-demo/echarts-demo-pie.component";
import { EchartsDemoClickComponent } from "app/showcase/custom-components/echarts-demo/echarts-demo-click.component";

@NgModule({
	declarations:[
		EchartsDemoComponent,
		EchartsDemoMapComponent,
		EchartsDemoBarComponent,
		EchartsDemoOptionsComponent,
		EchartsDemoPieComponent,
		EchartsDemoClickComponent,
		EchartsDemoLineComponent
	],
	imports: [
		EchartsDemoRoutingModule,
		CommonModule,
		FormsModule,
		NzGridModule,
		NzToolTipModule,
		NzCodeBoxModule,
		EchartsModule
	]
})
export class EchartsDemoModule { }



